<template>
  <div class="training">
    <h1>Math training</h1>
    <h1 :class="hideElement()">level {{level + 1}}</h1>
    <hr>
    <div class="progress" :class="hideElement()">
        <div class="progress-bar" :style="progressStyle" ></div>
      </div>
    <div class="box">
      
      <transition name="flip" mode="out-in">
        <app-start-screen v-if="state == 'start'"
        @onStart="onStart">
          
        </app-start-screen>
        <app-question v-else-if="state == 'question'"
                      @success="onQuestSuccess"
                      @error="onQuestError"
                      :settings="levels[level]"
        ></app-question>
        <app-message v-else-if="state == 'message'"
                     :type="message.type"
                     :text="message.text"
                     @onNext="onNext"
        ></app-message>
        <app-result-screen v-else-if="state == 'result'"
           :stats="stats"
           :messageResult="messageResult"
           @repeat="onStart"
           @levelUp="levelUp"
           @repeatLevel="finish"
        ></app-result-screen

          >
        <div v-else>Unknown state</div>
      </transition>
    </div>
    <hr>
    <button class="btn btn-danger" 
            :class="hideElement()"
            @click='state = "start"'
            >
          End game</button>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      state: 'start',
      stats: {
        success: 0,
        error: 0
      },
      level: 0,
      levels: [
        {
          variants: 2,
          range: 5,
          from: 10,
          to: 30
        },
        {
          variants: 4,
          range: 3,
          from: 50,
          to: 150
        },
        {
          variants: 6,
          range: 20,
          from: 246,
          to: 459
        },
      ],
      message:{
        type: '',
        text: '',
      },
      messageResult: {
        class: '', //my
        status: '',
        hide: ''
      },
      questMax: 3,
    }
  },
  computed: {
    questDone(){
      return this.stats.success + this.stats.error;
    },
    progressStyle(){
      return{
        width: (this.questDone / this.questMax * 100) + '%'
      };
    }

  },
  methods: {
    hideElement(){
      if(this.state == 'start'){
        return 'hide';
      }
      else{
        return false;
      }
    },
    finish(){
      this.state = 'question';
      this.stats.error = 0;
      this.stats.success = 0;
    },
    onStart(){
      this.level = 0;
      this.finish();
    },
    onQuestSuccess(){
      this.state = 'message';
      this.message.text = 'Good Job!';
      this.message.type = 'success';
      this.stats.success++;
    },
    onQuestError(msg){
      this.state = 'message';
      this.message.text = msg;
      this.message.type = 'warning';
      this.stats.error++;

    },
    onNext(){
      if(this.questDone < this.questMax){
        this.state = 'question';
      }
      else{
        this.state = 'result';//backup
        if(this.stats.success < this.questDone * 0.5){
          this.messageResult.class = 'warning';
          this.messageResult.status = 'REPEAT THE LEVEL';
          this.messageResult.hide = "hide";
        }
        else {
          this.messageResult.class = 'success';
          this.messageResult.status = 'GOOD';
            if(this.level < this.levels.length-1){
              this.messageResult.hide = "";
            }
        }
      }
    },
    levelUp(){
      this.finish();
      this.level++;
      this.messageResult.hide = "hide";
    },
  }
}
</script>

<style scoped>
  .training{
    max-width: 700px;
    margin: 20px auto;
  }
  

  .box{
    margin: 10px 0;
  }
  
  .hide{
    display: none
  }

  .flip-enter{

  }

  .flip-enter-active{
    animation: flipInX 0.3s linear;
  }

  .flip-leave{

  }

  .flip-leave-acive{
    animation: flipOutX 0.3s linear;
  }

  @keyframes flipInX{
    from{transform: rotateX(90deg);}
    to{transform: rotateX(0deg);}
  }

  @keyframes flipOutX{
    from{transform: rotateX(0deg);}
    to{transform: rotateX(90deg);}
  }
</style>



