<template>
	<div class="alert" :class="'alert-' + messageResult.class">
		<h3>{{messageResult.status}}</h3>
		<p>Errors: {{ stats.error }}</p>
		<p>Success: {{ stats.success }}</p>
		<button class="btn btn-warning
		"
						@click="$emit('repeat')"
		>restart Game</button>	
		<button class="btn btn-danger
		"
						@click="$emit('repeatLevel')"
		>restart level</button>		
		<button class="btn btn-success
		" :class="messageResult.hide"
						@click="$emit('levelUp')"
		>level UP</button>
	</div>
</template>

<script>
	export default{
		props: ['stats', 'messageResult']
	}
</script>

<style scoped>
	.alert{
		text-align: center;
	}

	  .hide{
    display: none;
  }

</style>


